import { useState, useEffect } from 'react';
import cl from "./DayForSсhedule.module.css"
import Switch from 'react-ios-switch';
import { observer } from "mobx-react-lite"
import { HeadlineBody } from '../../../../textStyles/TextStyleComponents';
import { useNavigate } from 'react-router-dom';
import getStore from '../../../../store/GetStore';
import templateStore from '../../../../store/TemplatesStore';

const DayForSсhedule = observer(({ key, day, isLast }) => {
    const tg = window.Telegram.WebApp;
    const navigate = useNavigate();

    const switchId = `switch-${day.day}.${day.month + 1}.${day.year}`;

    const monthNames = ["января", "февраля", "марта", "апреля", "мая", "июня", "июля", "августа", "сентября", "октября", "ноября", "декабря"];
    const [switchState, setSwitchState] = useState(false);



    const findDayStatus = (year, month, day, time) => {
        const dateString = `${day}.${month + 1}.${year}`;
        const dayInfo = time.find(d => d.day === dateString);
        return dayInfo ? dayInfo.status : null;
    };

    useEffect(() => {
        const status = findDayStatus(day.year, day.month, day.day, getStore.currentTimeData);
        setSwitchState(status === "WORK");
        
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [day, getStore.currentTimeData]);

  

    const handleClickToEditDay = () => {
        const date = `${day.day}.${day.month + 1}.${day.year}`
        navigate(`/dayOfSchedule/${date}`);
    }

    const handleLabelClick = (e) => {
        e.stopPropagation();
    };

    const toggleDayStatus = () => {
        const dateString = `${day.day}.${day.month + 1}.${day.year}`;
        const newStatus = switchState ? 'REST' : 'WORK';
        let updated = false;

        const template = templateStore.templatesData.find(t => t.id === 1);

        // Преобразуем gaps в recording_cells
        const recording_cells = template ? gapsToRecordingCells(template.gaps) : [];

        // Обновляем статус для существующего дня или добавляем новый день
        const updatedTime = getStore.currentTimeData.map((item) => {
            if (item.day === dateString) {
                updated = true;
                return { ...item, status: newStatus };
            }
            return item;
        });
    
        // Если день не найден, добавляем его
        if (!updated) {
            updatedTime.push({
                day: dateString,
                status: newStatus,
                working_minutes: 600,
                templateId: 1,
                recording_cells: recording_cells
            });
        }
    
        // Сортировка time по дате, возможно, потребуется корректировка в зависимости от формата даты
        updatedTime.sort((a, b) => new Date(a.day.split('.').reverse().join('-')) - new Date(b.day.split('.').reverse().join('-')));
    
        getStore.setCurrentTimeData(updatedTime);
        setSwitchState(!switchState);
    };

    const gapsToRecordingCells = (gaps) => {
        const recordingCells = [];
        gaps.forEach(gap => {
            let [startHours, startMinutes] = gap.start.split(':').map(Number);
            let [endHours, endMinutes] = gap.end.split(':').map(Number);
    
            while (startHours < endHours || (startHours === endHours && startMinutes < endMinutes)) {
                recordingCells.push({
                    time: `${String(startHours).padStart(2, '0')}:${String(startMinutes).padStart(2, '0')}`,
                    status: 'free'
                });
                startMinutes += 15;
                if (startMinutes >= 60) {
                    startMinutes = 0;
                    startHours += 1;
                }
            }
        });
        return recordingCells;
    };


    return (
        <div className={cl.container} key={key} onClick={handleClickToEditDay}>
            <HeadlineBody>{`${day.day} ${monthNames[day.month]}, ${day.shortDayOfWeek}`}</HeadlineBody>
            <label htmlFor={switchId} onClick={handleLabelClick}>
                <div>

                    <Switch
                        id={switchId} // Присваиваем свитчеру уникальный id
                        className={`${tg.colorScheme === 'dark' ? cl.customSwitchDark : cl.customSwitchLight} ${switchState ? cl.customSwitchActive : ''}`}
                        checked={switchState}
                        onChange={toggleDayStatus}
                    />

                </div>
            </label>
            {!isLast && <div className={cl.borderBottom}></div>}
        </div>
    );
})

export default DayForSсhedule;